import React from "react";
import Card from "../Card/Card";
import importAllImages from '../Helpers/ImportAllImages';
import hotelsCardData from './HotelsData.json';

const hotelsImages = importAllImages(require.context('../PartnersLogos/Hotels', false, /\.(webp)$/));

const Hotels = () => {
    return (
        <>
            <h2>Hotels</h2>
            <div className="section-container">
                {hotelsImages.map((hotelImage, index) => (
                    <Card
                        classAdded={"card-for-all"}
                        key={index}
                        image={hotelImage}
                        partner_name={hotelsCardData.partner_name[index]}
                    />
                ))}
            </div>
        </>
    );
}

export default Hotels;